<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container mb-4">
      <!--      <div class="title_ans">-->
      <!--        Тест на темперамент-->
      <!--      </div>-->
      <div class="ans_card" v-if="is_response">
        <div class="row">
          <div class="col-lg-6">
            <h2 class="section-title">Test natijasi</h2>
          </div>
        </div>
        <div class="row align-items-center my-4 pr-0 col-md-12">
          <div class="JumbotronOfFirstTestResult__Jumbotron-sc-6tf6lm-0 irvewj">
            <div class="JumbotronOfFirstTestResult__StyledRow-sc-6tf6lm-1 gDLYMP container row">
              <div class="col-lg-12 col-md-12">
                <div class="desc-bg">
                  <p v-for="(item,index) in an_res" :key="index">{{item}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="!is_response">

      <div class="on_test_title">{{ $store.state.testsId.title }}</div>
      <div class="on-test">
        <div class="one_test_progress">
          <div class="on-test-count">
            {{ activeIndex+1 }}/{{ $store.state.test_one.length }}
          </div>
          <div class="on-test-range">
            <range-slider
                class="slider"
                min="0"
                :max="$store.state.test_one.length"
                step="1"
                v-model="activeIndex">
            </range-slider>
          </div>
          <div class="on-test-card" v-for="(test, i) in  $store.state.test_one" :key="test.id">
            <div v-show="activeIndex == i">
              <div>
                <div class="on-test-quiz">
                  {{ test.title }}
                </div>
                <div class="answers_quiz" v-for="(option1,ops) in choices" :key="ops" >
                  <div class="ans_wer_h">
                    {{option1.letter}}
                  </div>
                  <div class="answer_mains_f">

                    <div class="answer_mains" v-for="(inds,inda) in option1.skill" :key="inda">
                      <label class="ans_main_label container_input"
                             :for="`option${inds.id}${test.id}`"
                      >
                        <input type="radio"

                               :id="`option${inds.id}${test.id}`"
                               :name="`op${ops}`"
                               @input="myFunc(option1,ops,inda,test)"
                        >
                        <span class="checkmark"></span>
                        <span class="ans_text">{{inds.text}}</span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div class="on-test-button">
                <button class="prev" @click="prevStart()">

                  <span>Odldingisi</span>

                </button>
                <button v-if="activeIndex==($store.state.test_one.length-1)"  class="next actives" @click="endTest()">
                  <span>Tugatish</span>
                </button>
                <button v-else class="next " :class="{'actives':skill&&attitude_child&&professional_wish}" @click="nextStart(test,i)">
                  <span>Keyngisi</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header, RangeSlider},
  data() {
    return {
      ans_quiz: '',
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn: [],
      choices:[
        {
          "id":1,
          "letter":"a",
          "skill":[
            {
              "id":11,
              "skill_child": 3,
              "text":"Men qoida bo'yichabajaraman"
            },
            {
              "id":12,
              "skill_child": 2,
              "text":"O'rtacha bajaraman",
            },
            {
              "id":13,
              "skill_child": 1,
              "text":"Men buni umuman bajara olmayman",
            },
          ],
        },
        {
          "id":2,
          "letter":"b",

          "skill":[
            {
              "id":14,
              "attitude_child": 3,
              "text":"Ijobiy",
            },
            {
              "id":15,
              "attitude_child": 2,
              "text":"Befarq",
            },
            {
              "id":16,
              "attitude_child": 1,
              "text":"Salbiy",
            },
          ],
        },
        {

          "id":3,
          "letter":"c",
          "skill":[
            {
              "id":17,
              "professional_wish": 3,
              "text":"Xa",
            },
            {
              "id":18,
              "professional_wish": 2,
              "text":"Baribir",
            },
            {
              "id":19,
              "professional_wish": 1,

              "text":"Yo'q",
            },
          ],
        }
      ],
      an_res: {},
      is_response: false,
      series: [{
        name: '',
        data: [55, 44, 5, 90, 45, 5, 90],
      }],
      chartOptions: {
        chart: {
          height: 100,
          type: 'radar',
        },
        xaxis: {
          categories: ['A:Art-Sanat', 'B:Business-Biznes', 'E:Experimental-experimental faolyat', 'O:Organisation-Tashkilot', 'P:Physical-jismoniy ish', 'Social-ijtimoiy ish', 'W:Words-so\'zlar']
        }
      },
      option:'',
      answers:[],
      quiz_id:'',
      skill:'',
      attitude_child:'',
      professional_wish:''
    }
  },
  mounted() {
    this.$store.dispatch('getQuizOne', this.$route.params.id)
    this.max = this.$store.state.test_one.length
    this.$store.dispatch('getTestsId', this.$route.params.id)
    console.log(this.choices,"ans")
  },
  methods: {
    myFunc(option1,ops,inda,test){
     this.quiz_id=test.id
      if(ops==0){
        console.log(option1.skill[inda].skill_child)
        this.skill=option1.skill[inda].skill_child
      }
      if(ops==1){
        console.log(option1.skill[inda].attitude_child)
        this.attitude_child=option1.skill[inda].attitude_child
      }
      if(ops==2){
        console.log(option1.skill[inda].professional_wish)
        this.professional_wish=option1.skill[inda].professional_wish
      }
    },
    generateReport() {
      this.$refs.html2Pdf.generatePdf()
    },
    getAns(item) {
      console.log()
      this.answer[this.sliderValue - 1] = {
        "question_id": this.$store.state.test_one[this.sliderValue - 1].id,
        "value": Number(item)
      }
      console.log(typeof Number(item))

    },
    nextStart() {

      if (this.activeIndex < this.$store.state.test_one.length) {
        this.activeIndex++
      }
      this.answer.push({
        "question_id":this.quiz_id,
        "skill": this.skill,
        "attitude":this.attitude_child,
        "professional_wish":this.professional_wish
      })
      console.log(this.answer)
      this.skill=''
      this.attitude_child=''
      this.professional_wish=''

    },
    endTest() {
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
          .then(res => {
            this.an_res = res.data.result
            this.is_response = true
          })
    },
    getOnetest() {
      this.is_response=false
      this.activeIndex=0
      this.answer=[]
      this.answers=[]

    },
    prevStart() {
      if (this.activeIndex > 0) {
        this.activeIndex--
      }
    }
  }
}
</script>
